.App{
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: 600;
    height: 100vh;
    margin-top: -30px;
}


.loading{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    /* margin-top: 200px; */
    height: 100%;
        width: 100%;
        margin-top: 20%;
}
.loadingContainer {
    position:relative;
    margin:100px auto 0 auto;
    padding-top:60px;
    width:110px;
}
.loadingContainer div {
    height:20px;
    width:20px;
    border-radius:50%;
    background: black;
    float:left;
    margin:0 3px;
    background:#6859a3;
}
.loadingContainer .ball1 {
    z-index:1;
    -webkit-animation: bounce 3s infinite ease-in-out;
    animation: bounce 3s infinite ease-in-out ;
}
.loadingContainer .ball2 {
    -webkit-animation: bounce 3s infinite ease-in-out;
    animation: bounce 3s infinite ease-in-out ;
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
}
.loadingContainer .ball3 {
    -webkit-animation: bounce 3s infinite ease-in-out;
    animation: bounce 3s infinite ease-in-out ;
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
}
.loadingContainer .ball4 {
    -webkit-animation: bounce 3s infinite ease-in-out;
    animation: bounce 3s infinite ease-in-out ;
    -webkit-animation-delay: 1.5s;
    animation-delay: 1.5s;
}

@keyframes bounce{
    0%,15% {-moz-transform: translate(0,0);}
    50% {-moz-transform: translate(0,-30px);background:#72c2c2;}
    85%, 100% {-moz-transform: translate(0,0);};
}
@-webkit-keyframes bounce {
    0%,20% {-webkit-transform: translate(0,0);}
    50% {-webkit-transform: translate(0,-30px);background:#72c2c2;}
    80%, 100% {-webkit-transform: translate(0,0);};
}

@keyframes bounce {
    0%,20% {-webkit-transform: translate(0,0);}
    50% {-webkit-transform: translate(0,-30px);background:#72c2c2;}
    80%, 100% {-webkit-transform: translate(0,0);};
}

@media (max-width:768px){

    .loading{
        height: 100%;
        width: 100%;
        margin-top: 50%;
    }


}


